<template>
    <div>
      <!-- 卡片视图 -->
      <el-card>
        <el-button type="primary" icon="el-icon-edit" style="margin-bottom: 20px;" @click="addDataS">分配房屋</el-button>
        <!-- <el-input placeholder="请输入内容" suffix-icon="el-icon-search" v-model="searchPage.s"
            style="width: 300px;margin-left: 20px;" @change="searchBook">
          </el-input> -->
        
        <!-- 角色列表区域 -->
        <el-table :data="dataS" border stripe>
          <el-table-column label="房屋登记id" prop="id"></el-table-column>
          <el-table-column label="业主id" prop="userId"></el-table-column>
          <el-table-column label="房屋id" prop="roomId"></el-table-column>
          
          <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" icon="el-icon-delete" @click="delDataS(scope.row.roomId)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 卡片视图结束 -->
      <!-- 分配房屋给业主开始 -->
      <el-dialog title="分配房屋" :visible.sync="dialogFormVisible">
        <el-form :model="insertdataS">
          <el-form-item label="用户id" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.userId" autocomplete="off" placeholder="用户id"></el-input>
          </el-form-item>
          <el-form-item label="房间id" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.roomId" autocomplete="off" placeholder="房间id"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="insertuser">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 新增结束 -->
      <!-- 取消分配房屋开始 -->
      <!-- <el-dialog title="修改信息" :visible.sync="UDPdialogFormVisible">
        <el-form :model="updatedataS">
          <el-form-item label="楼号" :label-width="formLabelWidth">
            <el-input v-model="updatedataS.#" autocomplete="off" placeholder="楼号"></el-input>
          </el-form-item>
          <el-form-item label="房号" :label-width="formLabelWidth">
            <el-input v-model="updatedataS.#" autocomplete="off" placeholder="房号"></el-input>
          </el-form-item>
          <el-form-item label="面积" :label-width="formLabelWidth">
            <el-input v-model="updatedataS.#" autocomplete="off" placeholder="面积"></el-input>
          </el-form-item>
          <el-form-item label="是否入驻" :label-width="formLabelWidth">
            <el-input v-model="updatedataS.#" autocomplete="off" placeholder="是否入驻"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="UDPdialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="updateS">确 定</el-button>
        </div>
      </el-dialog> -->
      <!-- 修改结束 -->
    </div>
  </template>
  <script>
    export default {
      data() {
        return {
          /* 数据库大作业-重用的修改 */
          /* 初始化渲染的表单 */
          dataS: {},
          /* 新增数据的表单 */
          insertdataS: {
            userId:"",
			roomId:""
          },
          dialogFormVisible: false,
          formLabelWidth: '120px',
          /* 修改数据获取的表单 */
          UDPdialogFormVisible: false,
          updatedataS: {
  
          },
          /* 多表时获取类型 */
          kinds:[]
        }
      },
      created() {
        this.init()
      },
      methods: {
        /* 初始化 */
        async init() {
          const {
            data: res
          } = await this.$http.post('query/userRoom', this.page)
		  console.log(res)
          this.dataS = res.data
        },
        async addDataS() {
          this.dialogFormVisible = true
          
        },
        async insertuser(){
        	
        	const {
        	  data: res
        	} = await this.$http.post('admin/sendRoom', this.insertdataS)
        	if (res.code != 200) {
        	  this.$message.error("添加失败")
        	}
        	console.log("res:" + res)
        	console.log(this.insertdataS)
        	this.$message.success("添加成功!")
        	this.dialogFormVisible = false
        	this.insertdataS = {}
        	this.init() 	
        },
		async delDataS(roomId) {
		  const {
		    data: res
		  } = await this.$http.post('admin/cancelRoom', {
		    "id": roomId
		  })
		  if (res.code != 200) {
		    this.$message.error("删除失败")
		  }
		  this.$message.success("删除成功!")
		  this.init()
		},
      }
    }
  
  </script>
  <style>
    .el-tag+.el-tag {
      margin-left: 10px;
    }
  
    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }
  
    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
  
  </style>