<template>
    <div>
      <!-- 卡片视图 -->
      <el-card>
        <el-button type="primary" icon="el-icon-edit" style="margin-bottom: 20px;" @click="addDataS">新增业主</el-button>
        <!-- <el-input placeholder="请输入内容" suffix-icon="el-icon-search" v-model="searchPage.s"
            style="width: 300px;margin-left: 20px;" @change="searchBook">
          </el-input> -->
       
        <!-- 角色列表区域 -->
        <el-table :data="dataS" border stripe>
         <el-table-column label="身份证号" prop="userId"></el-table-column>
         <el-table-column label="姓名" prop="name"></el-table-column>
         <el-table-column label="性别" prop="sex"></el-table-column>
         <el-table-column label="工作单位" prop="work"></el-table-column>
         <el-table-column label="电话" prop="phone"></el-table-column>
         <el-table-column label="家庭人口" prop="family"></el-table-column>
          <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
              
              <el-button size="mini" type="danger" icon="el-icon-delete" @click="delDataS(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 卡片视图结束 -->
      <!-- 新增开始 -->
      <el-dialog title="新增信息" :visible.sync="dialogFormVisible">
        <el-form :model="insertdataS">
			<el-form-item label="用户名" :label-width="formLabelWidth">
			  <el-input v-model="insertdataS.username" autocomplete="off" placeholder="用户名"></el-input>
			</el-form-item>
			<el-form-item label="密码" :label-width="formLabelWidth">
			  <el-input v-model="insertdataS.password" autocomplete="off" placeholder="密码"></el-input>
			</el-form-item>
          <el-form-item label="身份证号" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.userId" autocomplete="off" placeholder="身份证号"></el-input>
          </el-form-item>
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.name" autocomplete="off" placeholder="姓名"></el-input>
          </el-form-item>
          <el-form-item label="性别" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.sex" autocomplete="off" placeholder="性别"></el-input>
          </el-form-item>
          <el-form-item label="工作单位" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.work" autocomplete="off" placeholder="工作单位"></el-input>
          </el-form-item>
          <el-form-item label="电话" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.phone" autocomplete="off" placeholder="电话"></el-input>
          </el-form-item>
          <el-form-item label="家庭人口" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.family" autocomplete="off" placeholder="家庭人口"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="insertuser">确 定</el-button>
        </div>
      </el-dialog>
       
    </div>
  </template>
  <script>
    export default {
      data() {
        return {
          /* 数据库大作业-重用的修改 */
          /* 初始化渲染的表单 */
          dataS: {},
          /* 新增数据的表单 */
          insertdataS: {
            password:"",
            username:"",
            name:"",
            sex:"",
			      userId:"",
            phone:"",
            family:"",
            work:""
          },
          dialogFormVisible: false,
          formLabelWidth: '120px',
         
        }
      },
      created() {
        this.init()
      },
      methods: {
        /* 初始化 */
        async init() {
          const {
            data: res
          } = await this.$http.post('query/user',)
		  console.log(res.data)
          this.dataS = res.data
        },
        /* 删除 */
        async delDataS(id) {
          const {
            data: res
          } = await this.$http.post('admin/deleteUser', {
            "id": id
          })
          if (res.code != 200) {
            this.$message.error("删除失败")
          }
          this.$message.success("删除成功!")
          this.init()
        },
        /* 新增 */
        async addDataS() {
          this.dialogFormVisible = true
          
        },
		async insertuser(){
			
			const {
			  data: res
			} = await this.$http.post('admin/addUser', this.insertdataS)
			if (res.code != 200) {
			  this.$message.error("添加失败")
			}
			console.log("res:" + res)
			console.log(this.insertdataS)
			this.$message.success("添加成功!")
			this.dialogFormVisible = false
			this.insertdataS = {}
			this.init()
			
			
		},
        /* 点击修改 */
        /* async updateS(msg) {
          console.log(msg)
          this.updatedataS = msg */
          /* 打开修改对话框的操作 */
          /* this.UDPdialogFormVisible = true
          const {
            data: res
          } = await this.$http.post('#/#', {
            "id": this.UDPTable
          })
          if (res.code !== 200) {
            this.$message.error("修改失败！")
          }
          this.$message.success("修改成功！")
        }, */
      }
    }
  
  </script>
  <style>
    .el-tag+.el-tag {
      margin-left: 10px;
    }
  
    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }
  
    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
  
  </style>