<template>
    <div>
      <!-- 卡片视图 -->
      <el-card>
       <el-button type="primary" icon="el-icon-edit" style="margin-bottom: 20px;" @click="addDataS">处理物管需求</el-button>   
        <!-- 角色列表区域 -->
        <el-table :data="dataS" border stripe>
          <el-table-column label="需求单id" prop="id"></el-table-column>
          <el-table-column label="需求内容" prop="content"></el-table-column>
          <el-table-column label="业主id" prop="userId"></el-table-column>
          <el-table-column label="是否已被处理" prop="status"></el-table-column>
					<el-table-column label="负责人名字" prop="adminName"></el-table-column>
					<el-table-column label="提交时间" prop="time"></el-table-column>
					<el-table-column label="处理结果（）" prop="result"></el-table-column>
         
        </el-table>
      </el-card>
      <!-- 卡片视图结束 -->
      <!-- 新增开始 -->
      <el-dialog title="处理物管需求" :visible.sync="dialogFormVisible">
        <el-form :model="insertdataS">	
          <el-form-item label="物管需求编号" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.id" autocomplete="off" placeholder="物管需求编号"></el-input>
          </el-form-item>
          <el-form-item label="是否已被处理" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.status" autocomplete="off" placeholder="是否已被处理"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="insertuser">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 新增结束 -->
    </div>
  </template>
  <script>
    export default {
      data() {
        return {
          /* 数据库大作业-重用的修改 */
          /* 初始化渲染的表单 */
          dataS: {},
          /* 新增数据的表单 */
          insertdataS: {
            id:"",
			status:""
          },
          dialogFormVisible: false,
          formLabelWidth: '120px',
          /* 修改数据获取的表单 */
          UDPdialogFormVisible: false,
          updatedataS: {
  
          },
          /* 多表时获取类型 */
          kinds:[]
        }
      },
      created() {
        this.init()
      },
      methods: {
        /* 初始化 */
        async init() {
          const {
            data: res
          } = await this.$http.post('query/repair', this.page)
          this.dataS = res.data
        },
        
        /* 新增 */
        async addDataS() {
          this.dialogFormVisible = true
          
        },
        async insertuser(){
        	
        	const {
        	  data: res
        	} = await this.$http.post('admin/updateRepair', this.insertdataS)
        	if (res.code != 200) {
        	  this.$message.error("添加失败")
        	}
        	console.log("res:" + res)
        	console.log(this.insertdataS)
        	this.$message.success("添加成功!")
        	this.dialogFormVisible = false
        	this.insertdataS = {}
        	this.init()
        	
        	
        },
      }
    }
  
  </script>
  <style>
    .el-tag+.el-tag {
      margin-left: 10px;
    }
  
    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }
  
    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
  
  </style>