<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="title">
        <strong>楼房管理系统</strong>
      </div>
      <!-- 登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <!-- <el-button @click="register">去注册</el-button> -->
          <el-button type="primary" @click="adminlogin">管理员登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button> 
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        // 这是登录表单的数据绑定对象
        loginForm: {
         
        },
        // 这是表单的验证规则对象
        loginFormRules: {
          // 验证用户名是否合法
          username: [{
              required: true,
              message: '请输入登录名称',
              trigger: 'blur'
            },
            {
              min: 1,
              max: 10,
              message: '长度在 2 到 10 个字符',
              trigger: 'blur'
            }
          ],
          // 验证密码是否合法
          password: [{
              required: true,
              message: '请输入登录密码',
              trigger: 'blur'
            },
            {
              min: 5,
              max: 15,
              message: '长度在 6 到 15 个字符',
              trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      // 点击重置按钮，重置登录表单
      resetLoginForm() {
        this.$refs.loginFormRef.resetFields()
      },
      /*register() {
        this.$router.push('/register')
        /* this.$message.success("去注册") */
      
      adminlogin() {
        this.$refs.loginFormRef.validate(async valid => {
          if (!valid) return
          const {
            data: res
          } = await this.$http.post('admin/login', this.loginForm) 
          if (res.code !== 200) return this.$message.error('登录失败！')
          console.log(this.loginForm)
          console.log(typeof(this.loginForm))  

          console.log(res) 
          // 1. 将登录成功之后的 token，保存到客户端的 sessionStorage 中
          //   1.1 项目中出了登录之外的其他API接口，必须在登录之后才能访问
          //   1.2 token 只应在当前网站打开期间生效，所以将 token 保存在 sessionStorage 中
          /* window.sessionStorage.setItem('token', res.data.token)
          window.sessionStorage.setItem('username', res.data.user.username)
          window.sessionStorage.setItem('perm', res.data.user.perm)
          window.sessionStorage.setItem('role', res.data.user.role)
          window.sessionStorage.setItem('state', res.data.user.state) */
          /* if (res.data.user.state == 0) {
            this.$message.error("你被封号了！")
          } */
          // 2. 通过编程式导航跳转到后台主页，路由地址是 /home
          /* console.log("直接跳过去") */
          
          var obj = res.data;
          var infostr = JSON.stringify(obj);//转换json
          sessionStorage.obj = infostr;
            this.$message.success('登录成功')
            this.$router.push('/home')
          
        })
      },
	  
	  
    }
  }

</script>

<style lang="less" scoped>
  .login_container {
    background-image: linear-gradient(to bottom right, #CCFBFF , #EF96C5);
    height: 100%;
  }

  .login_box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
    .avatar_box {
      height: 130px;
      width: 130px;
      border: 1px solid #eee;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0 0 10px #ddd;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #fff;
    }
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #eee;
      }
    

    .title {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 200%);
      font-size: 22px;
    }
  

  .login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .btns {
    display: flex;
    justify-content: flex-end;
  }

</style>
