<template>
    <div>
      <!-- 卡片视图 -->
      <el-card>
        <el-button type="primary" icon="el-icon-edit" style="margin-bottom: 20px;" @click="provide">发放物业缴费单</el-button>

       
        <!-- 角色列表区域 -->
        <el-table :data="dataS" border stripe>
          <el-table-column label="缴费单id" prop="id"></el-table-column>
          <el-table-column label="业主id" prop="userId"></el-table-column>
          <el-table-column label="房屋id" prop="roomId"></el-table-column>
          <el-table-column label="缴费金额" prop="value"></el-table-column>
          <el-table-column label="缴费时间" prop="time"></el-table-column>
		  <el-table-column label="是否缴费" prop="status"></el-table-column>
          <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
              
              <el-button size="mini" type="primary" round @click="confirm(scope.row.id)">确认缴费</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 卡片视图结束 -->
      <!-- 新增开始 -->
      <el-dialog title="新增信息" :visible.sync="dialogFormVisible">
        <el-form :model="insertdataS">
          <el-form-item label="用户id" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.userId" autocomplete="off" placeholder="用户id"></el-input>
          </el-form-item>
          <el-form-item label="房间id" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.roomId" autocomplete="off" placeholder="房间id"></el-input>
          </el-form-item>
          <el-form-item label="活动时间">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="缴费单时间" v-model="insertdataS.time" style="width: 100%;"></el-date-picker>
              </el-col>
            </el-form-item>
          <el-form-item label="价格" :label-width="formLabelWidth">
            <el-input v-model="insertdataS.value" autocomplete="off" placeholder="价格"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="insertreceipts">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 新增结束 -->
     
      <!-- 修改结束 -->
    </div>
  </template>
  <script>
    export default {
      data() {
        return {
          /* 数据库大作业-重用的修改 */
          /* 初始化渲染的表单 */
          dataS: {},
          /* 新增数据的表单 */
          insertdataS: {
            userId:"",
			roomId:"",
            value:"",
            time:"",
			
            
          },
          dialogFormVisible: false,
          formLabelWidth: '120px',
          /* 修改数据获取的表单 */
          UDPdialogFormVisible: false,
          updatedataS: {
  
          },
          /* 多表时获取类型 */
          kinds:[]
        }
      },
      created() {
        this.init()
      },
      methods: {
        /* 初始化 */
        async init() {
          const {
            data: res
          } = await this.$http.post('query/payment')
          this.dataS = res.data
        },
        
        async provide() {
          this.dialogFormVisible = true
          
        },
        async insertreceipts(){
        	
        	const {
        	  data: res
        	} = await this.$http.post('admin/addPay', this.insertdataS)
        	if (res.code != 200) {
        	  this.$message.error("添加失败")
        	}
        	console.log("res:" + res)
        	console.log(this.insertdataS)
        	this.$message.success("添加成功!")
        	this.dialogFormVisible = false
        	this.insertdataS = {}
        	this.init()	
        },
		/* 删除 */
		async confirm(id) {
		  const {
		    data: res
		  } = await this.$http.post('user/toPay', {
		    "id": id
		  })
		  if (res.code != 200) {
		    this.$message.error("缴费失败")
		  }
		  this.$message.success("缴费成功!")
		  this.init()
		},
        
      }
    }
  
  </script>
  <style>
    .el-tag+.el-tag {
      margin-left: 10px;
    }
  
    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }
  
    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
  
  </style>